/**
 * Dependency
 * - jquery
 * - X-CSRF-TOKEN - to avoid token mismatch
 */

function CartItemApi(logged_in_token, cart_token) {
    this.logged_in_token = logged_in_token;
    this.cart_token = cart_token;

    this.cartPermit = {
        logged_in_token: this.logged_in_token,
        cart_token: this.cart_token,

        // laravel token
        _token: $('meta[name="csrf-token"]').attr('content')
    };
}

CartItemApi.prototype = {
    /**
     * Get all cart items
     *
     * @param {function} callback
     * @return {void}
     */
    getCartItems: function(callback) {
        $.get("/api/shopping-cart/cart-items", this.cartPermit, callback);
    },

    /**
     * Add item to cart
     *
     * @param {json}   data {
     *     name [string],
     *     front_image [string],
     *     brand [string],
     *     item_id [int]
     *     block_pattern_id [int],
     *     neck option [string],
     *     description [string],
     *     type [string],
     *     builder_customization [string],
     *     design_sheet [string]
     * }
     * @param {function} callback
     * @return {void}
     */
    addToCart: function(data, callback) {
        $.post("/api/shopping-cart/cart-items/add-to-cart", $.extend({}, data, this.cartPermit), callback);
    },

    /**
     * Update item to cart
     *
     * @param {int} cart_item_id
     * @param {json}   data {
     *     builder_customization [string]
     * }
     * @param {function} callback
     * @return {void}
     */
    updateItem: function(cart_item_id, data, callback) {
        var params = $.extend({}, data, this.cartPermit, {_method: "PUT"});

        $.post("/api/shopping-cart/cart-items/"+cart_item_id+"/update", params, callback);
    },

    /**
     * Update image of item
     *
     * @param {int} cart_item_id
     * @param {string} image
     * @param {string} perspective (left,, front, back, right)
     * @param {function} callback
     * @return {void}
     */
    updateImage: function(cart_item_id, image, perspective, callback) {
        var image_obj = {};
        image_obj[perspective + "_image"] = image;
        var params = $.extend({}, image_obj, this.cartPermit, {_method: "PUT"});

        $.post("/api/shopping-cart/cart-items/"+cart_item_id+"/update-"+perspective+"-image", params, callback);
    },

    /**
     * Delete item to cart
     *
     * @param  {int}   cart_item_id
     * @param  {function} callback
     * @return {void}
     */
    deleteToCart: function(cart_item_id, callback) {
        $.post("/api/shopping-cart/cart-items/"+cart_item_id+"/delete-to-cart", $.extend({}, this.cartPermit, {_method: "DELETE"}), callback);
    },

    /**
     * Retain item to cart. Assume the cart have duplicate cart item.
     *
     * @param  {int}   cart_item_id
     * @param  {function} callback
     * @return {void}
     */
    retainItem: function(cart_item_id, callback) {
        $.post("/api/shopping-cart/cart-items/"+cart_item_id+"/retain-item", $.extend({}, this.cartPermit, {_method: "POST"}), callback);
    }
};